.projectcard5 {
    @import url("https://fonts.googleapis.com/css?family=Montserrat");
  
    h3 {
      margin: 10px 0;
      color:black;
    }
  
    h6 {
      margin: 5px 0;
      text-transform: uppercase;
      color:black;
    }
  
    p {
      font-size: 14px;
      line-height: 21px;
      color:black;
    }
  
    .card-container {
      background-color: #fff;
      border-radius: 50px;
      // box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.75);
      color: #000;
      padding-top: 30px;
      width: 300px;
      text-align: center;
      @media screen and (max-width:342px) {
        width: 250px;
      }
      @media screen and (max-width:295px) {
        width: 200px;
      }
    }
  
    .card-container .pro {
      color: #231e39;
      background-color: #febb0b;
      border-radius: 3px;
      font-size: 14px;
      font-weight: bold;
      padding: 3px 7px;
      position: absolute;
      top: 30px;
      left: 30px;
    }
  
    .card-container .round {
      border-radius: 50%;
    }
    .bord{
      border:1px solid #03bfcb;
    }
  
    button.primary {
      background-color: #03bfcb;
      border: 1px solid #03bfcb;
      border-radius: 3px;
      color: #231e39;
      font-family: Montserrat, sans-serif;
      font-weight: 500;
      padding: 10px 25px;
    }
  
    button.primary.ghost {
      background-color: transparent;
      color: #02899c;
    }
  
    .skills {
      border-radius:50px!important;
  }
  
  .skills ul {
      list-style-type: none;
      margin: 0;
      padding: 0;
      display:flex;
      flex-wrap:wrap;
      justify-content: space-evenly;
    }
  
    .skills ul li {
      border: 1px solid #02899c;
      border-radius: 3px;
      font-size: 12px;
      margin-top:0.5rem;
      // margin: 0 3px 3px 0;
      padding: 7px;
      color:#02899c;
    }
  
    footer {
      background-color: #222;
      color: #fff;
      font-size: 14px;
      bottom: 0;
      position: fixed;
      left: 0;
      right: 0;
      text-align: center;
      z-index: 999;
    }
  
    footer p {
      margin: 10px 0;
    }
  
    footer i {
      color: red;
    }
  
    footer a {
      color: #3c97bf;
      text-decoration: none;
    }
  }
  