.Card1{
    .team-boxed {
        color:#313437;
        background-color:#fff;
      }
      
      .team-boxed p {
        color:#7d8285;
      }
      .team-boxed h2 {
        font-weight:bold;
        margin-bottom:40px;
        padding-top:40px;
        color:inherit;
      }
      
      @media (max-width:767px) {
        .team-boxed h2 {
          margin-bottom:25px;
          padding-top:25px;
          font-size:24px;
        }
      }
      
      .team-boxed .intro {
        font-size:16px;
        max-width:500px;
        margin:0 auto;
      }
      
      .team-boxed .intro p {
        margin-bottom:0;
      }
      
      .team-boxed .people {
        padding:50px 0;
      }
      
      .team-boxed .item {
        text-align:center;
      }
      
      .team-boxed  .box {
        text-align:center;
        padding:30px;
        background-color:#fff;
        margin-bottom:30px;
        width:300px;
      }
      
      .team-boxed .item .name {
        font-weight:bold;
        margin-top:28px;
        margin-bottom:8px;
      }
      
      .team-boxed .item .title {
        text-transform:uppercase;
        font-weight:bold;
        letter-spacing:2px;
        font-size:13px;
      }
      
      .team-boxed .item .description {
        font-size:15px;
        margin-top:15px;
        margin-bottom:20px;
      }
      
      .team-boxed .item img {
        width:160px;
        height:160px
      }
      
      .team-boxed .social {
        font-size:18px;
        color:#a2a8ae;
      }
      
      .team-boxed .social a {
        color:inherit;
        margin:0 10px;
        display:inline-block;
        opacity:0.7;
      }
      
      .team-boxed .social a:hover {
        opacity:1;
      }
      
      
}
